import React from 'react';
import './index.css';
import Day from './Day';
import DayModal from './Day_modal';
import DayModalEdit from './Day_modal_edit';
import ModalInfo from './modal_info';
import Logo from './img/Logo_design_var02_01.png';
import { connect } from 'react-redux';
import { mapStateToProps, mapDispatchToProps } from './Redux';


class Presentational extends React.Component {
  constructor(props) {
    super(props);
    this.state = 
     {click: true, day: [1,2,3,4,5,7,8], info: false, "modal day": false, 'modal edit': false, edit: null,
      inputDay: '', inputTime: [], inputTheme: '', inputText: '' };
      
      this.handleInfo = this.handleInfo.bind(this);
      this.handleClick = this.handleClick.bind(this);
      this.handleModalInput = this.handleModalInput.bind(this);
      this.handleInput = this.handleInput.bind(this);
      this.handleSubmit = this.handleSubmit.bind(this);
      this.handleDelete = this.handleDelete.bind(this);
      this.handleModalEdit = this.handleModalEdit.bind(this);
      this.handleModalSubmit = this.handleModalSubmit.bind(this);
      this.onMouseOver = this.onMouseOver.bind(this);
  }
  handleInfo() {
    this.setState({
      info: !this.state.info
    })
  }
  handleClick() {                                                   // Changing the first day of the week
    this.setState({
      click: !this.state.click
    })
    const button = document.getElementById('btn_click')
    
    // alert(button.style.right)
    if (this.state.click === true) {
      button.style.right = -17 + 'px';
      setTimeout(() => this.setState({
        day: [8,1,2,3,4,5,7]
      }), 500);
    } 
    else {
      button.style.right = 17 + 'px';
      setTimeout(() => this.setState({
        day: [1,2,3,4,5,7,8]
      }) , 500);
     
    }
  }
  handleInput(event) {                                               // Input
    switch(event.target.name) {
      case 'day':
        this.setState({
          inputDay: event.target.value
        })
        break;
      case 'time':
        if (event.target.id === 'hours') {
          this.setState({
            inputTime: [event.target.value, this.state.inputTime[1]]
          })
        }
        else {
          this.setState({
            inputTime: [this.state.inputTime[0], event.target.value]
          })
        }
        break;
      case "theme":
        this.setState({
          inputTheme: event.target.value
        })
        break;
      case 'text':
        this.setState({
          inputText: event.target.value
        })
        break;
        default:
          return;
    }
  }
  handleSubmit() {                                             // Submitting the new note
    if (this.state.inputDay && this.state.inputTime[0] && this.state.inputTheme && this.state.inputTime[0] < 24) {
      let day = this.state.inputDay;
      let time = [...this.state.inputTime];
      let theme = this.state.inputTheme;
      let text = this.state.inputText;
        if (!time[1]) {
          time[1] = 0;
        }
      this.props.newNote([day,time,theme,text])
      document.getElementById(this.state.inputDay).checked = false;
      document.getElementById('hours').value = '';
      document.getElementById('minutes').value = '';
      this.setState({
        inputDay: '',
        inputTime: [],
        inputTheme: '',
        inputText: '',
        'modal day': false
      })
      }
      else {
        alert('You should select the Day of the week, valid Time and type in Event to be able to add a new note');
        let week = document.getElementById('week');
        let time = document.getElementById('time');
        let theme = document.getElementById('theme');
        week.classList.add('animation--red');
        time.classList.add('animation--red');
        theme.classList.add('animation--red');
        setTimeout(() => {
          week.classList.remove('animation--red')
        }, 3000);
        setTimeout(() => {
          time.classList.remove('animation--red')
        }, 3000);
        setTimeout(() => {
          theme.classList.remove('animation--red')
        }, 3000);
      }
    }
  handleModalInput() {                                      // Calling the window for adding notes
      this.setState({
        "modal day": !this.state['modal day']
      })
  }
  handleModalEdit(event) {                                  // Calling the window of editing
    if (this.state['modal edit'] === false) {
      const link = this.props.data[event.target.id.split(',')[0]]['data'][event.target.id.split(',')[1]];
      this.setState({
        'modal edit': true,
        edit: event.target.id.split(','),
        inputTime: link[1],
        inputTheme: link[2],
        inputText: link[3]
      })
    }
    else {
      this.setState({
        'modal edit': false,
        edit: null,
        inputTime: [],
        inputTheme: '',
        inputText: ''
      })
    }
  }
  handleModalSubmit() {                                    // Submitting changes after editing
    this.props.editNote([this.state.edit, this.state.inputTime, this.state.inputTheme, this.state.inputText])
    this.setState({
      edit: null,
      inputTime: [],
      inputTheme: '',
      inputText: '',
      'modal edit': false
    })
  }
  handleDelete(event) {                                   // Deleting the note
    this.props.deleteNote([event.target.id, event.target.value]);
  }
  onMouseOver(pos) {                                      // Displaying the description of the note (if exists)
    const text_inside = this.props.data[pos.target.id.split(',')[0]]['data'][pos.target.id.split(',')[1]][3];                          
    if (text_inside) {
      const obj = document.getElementById('ghost')
      const ghost__inner = document.getElementById('ghost__inner');
      var x = pos.clientX;
      var y = pos.clientY;
      obj.style.display = 'block';
      ghost__inner.innerHTML = text_inside;
      obj.style.left = x + 5 + 'px';
      obj.style.top = y + -46 + -(text_inside.length/1.7 - window.scrollY) + 'px';
      // console.log('x: ' + x + ' ' + 'y: ' + y);
    }
  }
  onMouseOut() {                                          // Hiding the element when the mouse is out of the trigger
    document.getElementById('ghost').style.display = 'none';
  }
  render() {
    let dayProp = this.props.data;
    const modalInfo = this.state.info ? <ModalInfo close={this.handleInfo} /> : null;
    const modalInput = this.state['modal day'] ? <DayModal style={this.state['day']} closePop={this.handleModalInput} change={this.handleInput} value={this.state} submit={this.handleSubmit} /> : null;
    const modalEdit = this.state['modal edit'] ? <DayModalEdit close={this.handleModalEdit} change={this.handleInput} data={this.props.data[this.state.edit[0]]['data'][this.state.edit[1]]} submit={this.handleModalSubmit}/> : null;
  return (
    <div>
      {modalInfo}
      {modalInput}
      {modalEdit}
      <div id="header">
        {/* <div> */}
          {/* <h1>Week Planner</h1>
          <h3>planner of the week</h3> */}
          <img className="img__logo" src={Logo} alt="Week Planner"/>
        {/* </div> */}
      </div>
       <div className="button__container">
         <div style={{height: '0px'}}>
           <div className="button__click__container">
            <button id="btn_click" className="button__click" onClick={this.handleClick}></button>
          </div>
         </div>
            <button className="button__add" onClick={this.handleModalInput}>
                      <div className="svg__container">
                        <svg width="180px" height="60px" viewBox="0 0 180 60" className="border">
                          <polyline points="179,1 179,59 1,59 1,1 179,1" className="bg-line" />
                          <polyline points="179,1 179,59 1,59 1,1 179,1" className="hl-line" />
                        </svg>
                      </div>
                    <span>New note</span>
          </button>
            <div style={{position: 'relative'}}>
              <button id='info' onClick={this.handleInfo}><i className="far fa-question-circle fa-2x"></i></button>
            </div>
        </div>
      <div className="container">
       <Day info={dayProp.sun} edit={this.handleModalEdit} delete={this.handleDelete} num={'sun'} mouse_in={this.onMouseOver} mouse_out={this.onMouseOut} style={{order: this.state['day'][0]}} />
       <Day info={dayProp.mon} edit={this.handleModalEdit} delete={this.handleDelete} num={'mon'} mouse_in={this.onMouseOver} mouse_out={this.onMouseOut} style={{order: this.state['day'][1]}} />
       <Day info={dayProp.tue} edit={this.handleModalEdit} delete={this.handleDelete} num={'tue'} mouse_in={this.onMouseOver} mouse_out={this.onMouseOut} style={{order: this.state['day'][2]}} />
       <Day info={dayProp.wed} edit={this.handleModalEdit} delete={this.handleDelete} num={'wed'} mouse_in={this.onMouseOver} mouse_out={this.onMouseOut} style={{order: this.state['day'][3]}} />
       <Day info={dayProp.thu} edit={this.handleModalEdit} delete={this.handleDelete} num={'thu'} mouse_in={this.onMouseOver} mouse_out={this.onMouseOut} style={{order: this.state['day'][4]}} />
       <div className="flex__break"></div>
       <Day info={dayProp.fri} edit={this.handleModalEdit} delete={this.handleDelete} num={'fri'} mouse_in={this.onMouseOver} mouse_out={this.onMouseOut} style={{order: this.state['day'][5]}} />
       <Day info={dayProp.sat} edit={this.handleModalEdit} delete={this.handleDelete} num={'sat'} mouse_in={this.onMouseOver} mouse_out={this.onMouseOut} style={{order: this.state['day'][6]}} />
      </div>
    </div>
  );
}
}

let App = connect(mapStateToProps, mapDispatchToProps)(Presentational);

export default App;

