import React from 'react';


class DayModalEdit extends React.Component {
    render() {
        return(
            <div>
            <div className="modal__day" onClick={this.props.close}></div>
                <div className='modal__inside'>
                    <button className='modal__button' onClick={this.props.close}><i style={{marginTop: '1px'}} class="far fa-window-close fa-lg"></i></button>
                    <h1>Editing</h1>
                    <div className="modal__container">   

                    {/*TIME*/}

                            <div className="modal__block" style={{height: '30px'}} id='time'>        
                                <p style={{marginTop: '0px'}}>Time</p>
                                <div style={{flexDirection: 'row'}}>
                                <input defaultValue={this.props.data[1][0]} type="number" id="hours" name="time" min={0} max={23} style={{'width': '35px', 'height': '20px'}} onChange={this.props.change} />
                                <input defaultValue={this.props.data[1][1]} type="number" id="minutes" name="time" min={0} max={59} style={{'width': '35px', 'height': '20px'}} onChange={this.props.change} placeholder="00" />
                                </div>
                            </div>
                           
                    {/*THEME */}      
                           
                            <div className="modal__block" style={{height: '30px'}} id='theme'>
                                    <p style={{marginTop: '0px'}}>Event</p>
                                    <input defaultValue={this.props.data[2]} type="text" id="theme" name='theme' size='18' maxLength='23' placeholder="23 symbols only" onChange={this.props.change} />
                            </div>
                            
                    {/*DESCRIPTION OF APPOINTMENT */}
                            
                            <div className="modal__block">
                                <p style={{marginTop: '0px', width: '700px'}}>Note</p>
                                <textarea defaultValue={this.props.data[3]} onChange={this.props.change} className='modal__note' name="text" rows="10" columns="20"  maxLength='200'
                                 placeholder="200 symbols only" />
                        </div>
                    </div>
                    <button className="button__submit" onClick={this.props.submit}>Edit</button>
                    <button className="button__submit" onClick={this.props.close}>Cancel</button>
                </div>
            </div>
        )
    }
}

export default DayModalEdit;