import React from 'react';
import inf01 from './img/inf01.png';
import inf02 from './img/inf02.png';
import inf03 from './img/inf03.png';

class ModalInfo extends React.Component {
    render() {
        return(
            <div>
            <div className="modal__day" onClick={this.props.close}></div>
                <div className='modal__inside'>
                    <button className='modal__button' onClick={this.props.close}><i style={{marginTop: '1px'}} className="far fa-window-close fa-lg"></i></button>
                    <h1>Info</h1>
                    <div className="modal__container info">   
                        <div style={{textAlign: 'center', margin: '0 10px 0 10px'}}>
                            <strong>Week Planner<sup>©</sup></strong> is capable of making new notes, 
                            editing and deleting created notes.
                        </div>
                        <ul>
                            <li>
                                <div>
                                    For your convenience, you can change the first day of the week using this button. <br />
                                    <img className="img__info" src={inf01} alt='inf01'></img>
                                </div>
                            </li>
                            <li>
                                <div>
                                    To add a new note click "New note", after that pick the day of the week, 
                                    valid time (hours: 0-23, minutes: 0-59), write down your event, description (optional) 
                                    and click "Add".
                                </div>
                            </li>
                            <li>
                                <div>
                                    To see the description of the event simply put your cursor on the event. <br />
                                    <img className="img__info" src={inf02} alt='inf01'></img> <br />
                                    You will see nothing if the description is absent.
                                </div>
                            </li>
                            <li>
                                <div>
                                    Click on the created event to edit it. <strong>Week Planner<sup>©</sup></strong> will automatically 
                                    arrange your events by time.
                                </div>
                            </li>
                            <li>
                                <div>
                                    To delete click "minus" icon. <br />
                                    <img className="img__info" src={inf03} alt='inf01'></img>
                                </div>
                            </li>
                            <div style={{textAlign: "center", marginTop: "10px"}}>
                                That's it! <br /> Make your week busy with important things and <em><strong>"don't waste you time or time 
                                will waste you!"</strong></em>
                            </div>
                        </ul>
                    </div>
                    <button className="button__submit" onClick={this.props.close}>Merci!</button>
                </div>
            </div>
        )
    }
}

export default ModalInfo;