import React from 'react';

class Day extends React.Component {
    render() {
        return(
            <div className='day__container' style={this.props.style}>
                <div id='ghost'><div className="ghost__chart"><em><p id='ghost__inner'></p></em></div></div>
                <div className="day__header">
                <h1>{this.props.info.day}</h1>
                </div>
                    <div className='day__chart'>
                        {(this.props.info.data[0]) ? (this.props.info['data'].map((x, index) => {
                            if (x[1][1].length === 1 | !x[1][1]) {
                                return <div><div onClick={this.props.edit} id={[this.props.num, index]} className="day__data" onMouseMove={this.props.mouse_in} onMouseOut={this.props.mouse_out}><p id={[this.props.num, index]}>{x[1][0]}-0{x[1][1]}: {x[2]}</p></div>
                                <div className="for--button"><button onClick={this.props.delete} className="button" key={x[1][0] + index} value={index} id={this.props.num}><i value={index} id={this.props.num} className="fas fa-minus-square fa-lg"></i></button></div></div>;
                            }
                            else {
                                return <div><div onClick={this.props.edit} id={[this.props.num, index]} className="day__data" onMouseMove={this.props.mouse_in} onMouseOut={this.props.mouse_out}><p id={[this.props.num, index]}>{x[1][0]}-{x[1][1]}: {x[2]}</p></div>
                                 <div className="for--button"><button onClick={this.props.delete} className="button" key={x[1][0] + index} value={index} id={this.props.num}><i value={index} id={this.props.num} className="fas fa-minus-square fa-lg"></i></button></div></div>;
                            }
                        })) : <h2 className='day__chart--font'>no notes</h2>}
                    </div>
            </div>
        )
    }
}

export default Day;