const ADD = "ADD";      // Adding info
const DEL = "DEL";      // Deleting info
const EDT = "EDT";      // Editing info

// INITIALIZING THE DATA - WEEK ****
// sun.day FOR THE RENDER ONLY

let week = {
    "sun": {
        day: "SUNDAY",       
        data: []
    },                       
    "mon": {                 
        day: "MONDAY",
        data: []
    },
    "tue": {                 
        day: "TUESDAY",
        data: []
    },
    "wed": {                 
        day: "WEDNESDAY",
        data: []
    },
    "thu": {                 
        day: "THURSDAY",
        data: []
    },
    "fri": {                 
        day: "FRIDAY",
        data: []
    },
    "sat": {                 
        day: "SATURDAY",
        data: []
    }
}

// ****

const addNote = (data) => {
    return {type: ADD,
            data}
}
const delNote = (data) => {
    return {type: DEL,
            data}
}
const edNote = (data) => {
    return {type: EDT,
            data}
}



 export const dayDataReducer = (state = week, action) => {
    const newState = {...state};
    switch(action.type) {
        case ADD:
                newState[action.data[0]]['data'].push(action.data);
                newState[action.data[0]]['data'].sort((a,b) => a[1][0] - b[1][0]);
                newState[action.data[0]]['data'].sort((a,b) => {
                    if (a[1][0] === b[1][0]) {
                        return a[1][1] - b[1][1];
                    }
                })
                return newState;
        case EDT:
                newState[action.data[0][0]]['data'][action.data[0][1]] = [[action.data[0][0]], action.data[1], action.data[2], action.data[3]];
                newState[action.data[0][0]]['data'].sort((a,b) => a[1][0] - b[1][0]);
                newState[action.data[0][0]]['data'].sort((a,b) => {
                    if (a[1][0] === b[1][0]) {
                        return a[1][1] - b[1][1];
                    }
                })
               return newState;
        case DEL:
                newState[action.data[0]]['data'].splice(action.data[1], 1);
                return newState;
        default: 
            return state;
    }
}

export const mapStateToProps = (state) => {
    return {
        data: state
    }
}
export const mapDispatchToProps = (dispatch) => {
    return {
        newNote: (data) => {
            dispatch(addNote(data))
        },
        deleteNote: (data) => {
            dispatch(delNote(data))
        },
        editNote: (data) => {
            dispatch(edNote(data))
        }
    }
}