import React from 'react';


class DayModal extends React.Component {
    render() {
        return(
            <div>
            <div className="modal__day" onClick={this.props.closePop}></div>
                <div className='modal__inside'>
                    <button className='modal__button' onClick={this.props.closePop}><i style={{marginTop: '1px'}} className="far fa-window-close fa-lg"></i></button>
                    <h1>Make a note</h1>
                    <div className="modal__container">   

                    {/*DAY OF THE WEEK */}

                            <div className="modal__block" id='week' style={{alignItems: 'flex-start' }}>
                                    <div style={{order: this.props.style[0]}} className="modal__label">
                                        <input  type="radio" id="sun" name="day" value="sun" onClick={this.props.change} /><label htmlFor="sun"><p>Sunday</p></label>
                                    </div>
                                    <div style={{order: this.props.style[1]}} className="modal__label">
                                        <input type="radio" id="mon" name="day" value="mon" onClick={this.props.change} /><label htmlFor="mon"><p>Monday</p></label>
                                    </div>
                                    <div style={{order: this.props.style[2]}} className="modal__label">
                                        <input type="radio" id="tue" name="day" value="tue" onClick={this.props.change} /><label htmlFor="tue"><p>Tuesday</p></label>
                                    </div>
                                    <div style={{order: this.props.style[3]}} className="modal__label">
                                        <input type="radio" id="wed" name="day" value="wed" onClick={this.props.change} /><label htmlFor="wed"><p>Wednesday</p></label>
                                    </div>
                                    <div style={{order: this.props.style[4]}} className="modal__label">
                                        <input type="radio" id="thu" name="day" value="thu" onClick={this.props.change} /><label htmlFor="thu"><p>Thursday</p></label>
                                    </div>
                                    <div style={{order: this.props.style[5]}} className="modal__label">
                                        <input type="radio" id="fri" name="day" value="fri" onClick={this.props.change} /><label htmlFor="fri"><p>Friday</p></label>
                                    </div>
                                    <div style={{order: this.props.style[6]}} className="modal__label">
                                        <input type="radio" id="sat" name="day" value="sat" onClick={this.props.change} /><label htmlFor="sat"><p>Saturday</p></label>
                                    </div>
                            </div>
                            
                    {/*TIME*/}

                            <div className="modal__block" id='time'>        
                                <p style={{marginTop: '0px'}}>Time</p>
                                <div style={{flexDirection: 'row'}}>
                                <input value={this.props.value['inputTime'][0]} type="number" id="hours" name="time" min={0} max={23} style={{'width': '35px', 'height': '20px'}} onChange={this.props.change} />
                                <input value={this.props.value['inputTime'][1]} type="number" id="minutes" name="time" min={0} max={59} style={{'width': '35px', 'height': '20px'}} onChange={this.props.change} placeholder="00" />
                                </div>
                            </div>
                           
                    {/*THEME */}      
                           
                            <div className="modal__block" id='theme'>
                                    <p style={{marginTop: '0px'}}>Event</p>
                                    <input value={this.props.value['inputTheme']} type="text" id="theme" name='theme' size='18' maxLength='23' placeholder="23 symbols only" onChange={this.props.change} />
                            </div>
                            
                    {/*DESCRIPTION OF APPOINTMENT */}
                            
                            <div className="modal__block">
                                <p style={{marginTop: '0px', width: '700px'}}>Note</p>
                                <textarea value={this.props.value['inputText']} onChange={this.props.change} className='modal__note' name="text" rows="10" columns="20"  maxLength='200'
                                 placeholder="200 symbols only" />
                        </div>
                    </div>
                    <button className="button__submit" onClick={this.props.submit}>Add</button>
                    <button className="button__submit" onClick={this.props.closePop}>Cancel</button>
                </div>
            </div>
        )
    }
}

export default DayModal;